<template>
  <div class="offer-card">
    <div class="card-heading">
      <div class="card-title">
        <b-row>
          <b-col cols="3">
            <img :src="item.property.images[0]" width="98px" height="80px" />
          </b-col>
          <b-col cols="6">
            <div class="name">{{ item.name }}</div>
            <div class="font-size-small font-weight-semi-bold light-gray">
              @ {{ item.property_owner.full_name }}
            </div>
          </b-col>
          <b-col cols="3" class="p-0">
            <div class="card-budget">
              <p class="mb-0 font-weight-bold">
                {{ item.budget }} {{ $t('common.egp') }}
              </p>
              <span class="font-size-smaller light-gray">
                {{
                  item.term === 1
                    ? $t('common.per_month')
                    : $t('common.per_night')
                }}
              </span>
            </div>
          </b-col>
        </b-row>
      </div>
    </div>

    <div class="d-flex mt-3 justify-content-start">
      <div>
        <svg-icon name="calendar" height="24" width="24" />
        <span class="font-size-small light-gray font-weight-bold">
          {{ item.duration + $t('common.from') }}
          {{ $dateFns.format(new Date(item.move_in_date), 'dd MMM yy') }}
        </span>
      </div>
    </div>
    <hr />
    <div class="card-actions">
      <div :class="`chip ${offerStatus}`">
        <div class="chip-label">
          <span>{{ offerStatus }}</span>
        </div>
      </div>
      <div class="gray">
        {{ $t('offer.viewOfferDetails') }}
        <svg-icon name="black-arrow-right" height="24" width="24" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  computed: {
    offerStatus() {
      if (this.item.status_details)
        return this.item.status_details.status_tenant;
      return '';
    },
  },
};
</script>

<style lang="scss" scoped>
.offer-card {
  background: white;
  border: 1px solid #e3e5e5;
  border-radius: 16px;
  padding: 24px;
  margin: 16px 0;

  .card-heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    .card-title {
      font-size: 16px;
      font-weight: bold;
      color: black;
      width: 100%;

      img {
        border-radius: 8px;
        object-fit: cover;
      }
    }

    .card-budget {
      text-align: center;

      p {
        color: #0078ff;
        text-align: center;
      }
    }
  }

  .card-actions {
    font-weight: bold;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .chip {
      height: 36px;
      left: 0;
      top: 0;
      box-sizing: border-box;
      cursor: pointer;
      display: inline-block;
      padding: 8px 12px;
      border-radius: 100px;

      .chip-label {
        font-weight: 600;
        font-size: 12px;
        line-height: 20px;
        margin: auto;
        vertical-align: middle;
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        text-align: center;
        text-transform: capitalize;
      }

      &.accepted {
        background-color: #def2e8;

        .chip-label {
          color: #00af48;
        }
      }

      &.rejected {
        background-color: #ffeeee;

        .chip-label {
          color: #ff5252;
        }
      }

      &.pending,
      &.negotiating {
        background-color: #fff9ea;

        .chip-label {
          color: #ffc025;
        }
      }
    }
  }
}
</style>
