<template>
  <div>
    <b-overlay :show="loading" rounded="sm" :opacity="1">
      <div class="request-offers">
        <div class="heading">
          <div class="container">
            <div>
              <p>
                {{ requestData.name }}
              </p>
              <span class="light-gray">
                {{ requestData.created_at }}
              </span>
            </div>

            <b-button
              size="sm"
              variant="primary"
              @click="
                () =>
                  $router.push(
                    localePath(`/dashboard/requests/${requestData.id}`)
                  )
              ">
              View details
            </b-button>
          </div>
        </div>
        <div class="recieved-offers">
          <div class="container">
            <div class="title">
              {{ $t('offer.received_offers') }}
            </div>
            <b-row v-if="haveOffers">
              <b-col
                xl="4"
                lg="6"
                cols="12"
                v-for="offer in offers"
                :key="offer.id">
                <NuxtLink
                  :to="
                    localePath(
                      `/dashboard/requests/${$route.params.requestId}/offers/${offer.id}`
                    )
                  ">
                  <TenantOfferCard :item="offer" />
                </NuxtLink>
              </b-col>
            </b-row>
            <div class="no-offers" v-else>
              <svg-icon name="smiley-meh" width="48" height="48"></svg-icon>
              <h3>You have not recieved any offers yet.</h3>
            </div>
          </div>
        </div>
      </div>
    </b-overlay>
  </div>
</template>

<script>
import TenantOfferCard from '@/components/dashboard/TenantOfferCard';
export default {
  name: 'RequestOffers',
  head() {
    return {
      title:
        !!this.requestData.name &&
        `Recieved Offers in ${this.requestData.name}`,
    };
  },
  components: {
    TenantOfferCard,
  },
  data() {
    return {
      requestData: {},
      offers: null,
      haveOffers: true,
      loading: false,
    };
  },
  async created() {
    this.loading = true;
    this.requestData = await this.$axios.$get(
      `/requests/${this.$route.params.requestId}`
    );
    this.requestData = this.requestData.Data;

    this.offers = await this.$axios.$get(
      `/user/requests/${this.$route.params.requestId}/received-offers`
    );
    this.offers = this.offers.Data.collection;
    if (this.offers && this.offers.length === 0) this.haveOffers = false;
    this.loading = false;
  },
};
</script>

<style lang="scss" scoped>
.request-offers {
  min-height: 100vh;
  background-color: #f7f9fa;

  .heading {
    background-color: white;
    padding: 32px 0;
    font-weight: bold;

    .container {
      display: flex;
      justify-content: space-between;
    }

    p {
      font-size: 18px;
      margin: 0;
    }

    .btn-primary {
      background-color: #e6f2ff;
      color: #0078ff;
      border: none;
      font-weight: bold;
      border-radius: 12px;
    }
  }

  .recieved-offers {
    padding: 16px 0;
    .title {
      color: #111111;
      font-weight: bold;
      font-size: 16px;
      margin: 0 8px;
    }

    .no-offers {
      text-align: center;
      margin: 64px 0;

      h3 {
        text-align: center;
        font-weight: bold;
        font-size: 18px;
        margin-top: 16px;
      }
    }
  }
}
</style>
